<template>
	<div id="cart">
		<van-sticky>
			<header :class='{"bgWhite":goodsList.length==0}'>
				<i class='iconfont icon-back' @click='$router.go(-1)'></i>
				<span>购物车</span>
			</header>
		</van-sticky>
		<div class="count" v-show='goodsList.length>0'>
			<p>
				<van-divider content-position="left"  :style="{ color: '#fff'}">共{{goodsList.length}}件商品</van-divider>
			</p>
		</div>
		<p class='empty' v-show='goodsList.length==0'>购物车空空如也~~</p>
		<van-checkbox-group v-model="selectedList"  ref="checkboxGroup">
			<div class='row' v-for='(val,i) in goodsList' :key='i' >
				<van-swipe-cell>
					<template slot="default">
						<div class="left">
							 <van-checkbox :name="val"  @click='single(val)'></van-checkbox>
						</div>
						<div class="right" >
							<img :src="val.img" alt="" @click='goGoods(val)'>
							<div class="info" @click='goGoods(val)'>
								<p class="title">{{val.name}}</p>
								<span class='spec'>{{val.spec}}</span>
								<p class="price">¥{{val.price}}</p>
								
							</div>
							<van-stepper v-model="val.number" :name='i' @change='changeNum'/>
							 <!-- @plus='plus(val,i)' @minus='minus(val,i)' -->
						</div>
					</template>
					<template slot="right">
						<van-button square type="danger" text="删除" @click='singleDelete(val)'/>
					</template>
				</van-swipe-cell>	
			</div>
		</van-checkbox-group>
		<van-submit-bar
		  	:price="total"
		    button-text="结算"
		    @submit='confirmOrder'
		>
			<template slot="default">
				<div class='btn'>
					<van-checkbox v-show='goodsList' v-model="ischeckAll" @click="checkAll(goodsList)">全选</van-checkbox >
					<van-button plain hairline type="primary" round v-show='selectedList.length>0' @click='MultiDelete'>删除</van-button>
				</div>		
			</template>
		</van-submit-bar>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:'cart',
		data(){
			return{
				selectedList: [],
				value:1,
				ischeckAll:false,
				goodsList:[],
				total:0,
				cart:[]
			}
		},
		methods:{
			changeNum(num,item){
				let cart_goodsList=JSON.parse(localStorage.getItem('cart_goodsList'));
		    	cart_goodsList[item.name].number=num;
		    	localStorage.setItem('cart_goodsList',JSON.stringify(cart_goodsList));
		    	this.sum();
			},
			// selectSingle(list){
			// 	// console.log(list)
			// 	if(list.length==0)
			// 		this.total=0;
			// 	list.forEach((val)=>{
			// 		this.total+=val.price*val.number*100;
			// 	})
				
			// },
			confirmOrder(){
				if(this.goodsList.length===0){
					this.$toast({
			    		type:'fail',
			    		message:'购物车目前空空如也~~',
		    		});
		    		return;
				}
				if(this.selectedList.length===0){
					this.$toast({
			    		type:'fail',
			    		message:'请先选择商品~~',
		    		});
		    		return;
				}
				else{
					localStorage.setItem('confirmOrder',JSON.stringify(this.selectedList));
					this.$router.push('/confirmOrder');
				}
			},
			goGoods(item){
				sessionStorage.setItem('goodsTitle',item.name);
		    	sessionStorage.setItem('goodsPrice',item.price);
		    	sessionStorage.setItem('goodsId',item.goodsId);
		    	sessionStorage.setItem('goodsImg',item.img);
		    	sessionStorage.setItem('goodsNumber',item.number);
		    	sessionStorage.setItem('goodsSpec',item.spec);
		    	this.$router.push({
		    		name:'goods'
		    	})
			},
			single(item){
				// 条件: 数组中是否包含该元素
				let isExist = this.selectedList.indexOf(item);
				if(isExist > -1){
					this.selectedList.splice(isExist,1);
				}else {
					// push 数组里
					this.selectedList.push(item);
				}
				if(this.selectedList.length===this.goodsList.length){
					this.ischeckAll=true;
				}
				else{
					this.ischeckAll=false;
				}
				this.sum();
			},
			checkAll(list) {
				if(this.ischeckAll){
					this.$refs.checkboxGroup.toggleAll();
					this.selectedList=[];
				}
				else{
					this.$refs.checkboxGroup.toggleAll(true);
					let arr = [];
					list.forEach((item) => {
						arr.push(item);
					})
					this.selectedList=arr;
				}
				this.sum();
			    this.ischeckAll=!this.ischeckAll;
		    },
		    sum(){// 合计
				this.total = 0;
				this.selectedList.forEach((item,i) => {
					this.total = this.total + (item.number * (item.price*100));
				})
				this.total  = this.total;
			},
		    // plus(val,index){
		    // 	let cart_goodsList=JSON.parse(localStorage.getItem('cart_goodsList'));
		    // 	val.number++;
		    // 	cart_goodsList[index].number=val.number;
		    // 	localStorage.setItem('cart_goodsList',JSON.stringify(cart_goodsList));
		    // 	this.sum();
		    // },
		    // minus(val,index){
		    // 	let cart_goodsList=JSON.parse(localStorage.getItem('cart_goodsList'));
		    // 	val.number--;
		    // 	cart_goodsList[index].number=val.number;
		    // 	localStorage.setItem('cart_goodsList',JSON.stringify(cart_goodsList));
		    // 	this.sum();

		    // },
		    singleDelete(item){
		    	// let cart_goodsList=localStorage.getItem('cart_goodsList');
		    	// this.cart=JSON.parse(cart_goodsList);
		    	// console.log(this.cart);
		    	// console.log(this.cart.indexOf(item));
		    	// console.log(item);
		    	// cart_goodsList.splice(cart_goodsList.indexOf(item),1);
		    	// cart_goodsList.__proto__=this.goodsList.__proto__;
		    	// console.log(cart_goodsList);
		    	// console.log(cart_goodsList.indexOf(item));
		 		//localStorage.setItem('cart_goodsList',JSON.stringify(cart_goodsList));

		  		// if(this.selectedList.length!=this.goodsList.length && this.selectedList.length>0){
		  		// 	this.total=0;
		  		// 	this.selectedList=[];
		  		// 	this.$toast({
			   //  		type:'success',
			   //  		message:'左滑删除不需要选中~~~',
		    // 		});
		  		// 	return ;
		  		// }
		  			// console.log(this.goodsList.indexOf(item));
		  			// console.log(this.selectedList.indexOf(item));
		  			console.log(this.selectedList.indexOf(item));
		  			// 删除的时候先判断选前选中的item是否存在
		  			let isExist = this.selectedList.indexOf(item);
		  			if(isExist > -1){
						this.selectedList.splice(isExist,1);
					}
		  			this.goodsList.splice(this.goodsList.indexOf(item),1);
		  			// this.selectedList.splice(this.selectedList.indexOf(item),1);

		    	// 更新数组
				localStorage.setItem('cart_goodsList',JSON.stringify(this.goodsList));
				this.ischeckAll = this.goodsList.length>0? this.ischeckAll : false;
				this.sum();
		    },
		    MultiDelete(){
		    	// 循环删除所有选中的商品
				while(this.selectedList.length > 0){
					this.singleDelete(this.selectedList[0]);
				}

				this.$toast({
		    		type:'success',
		    		message:'删除成功~~',
		    	});
				// 初始化数据
			
				this.selectedList = [];
				this.sum();
		    }
		},
		activated(){
			let goodsList=localStorage.getItem('cart_goodsList');
			if(goodsList){
				this.goodsList=JSON.parse(goodsList);	
			}
		},
		deactivated(){
			this.total=0;
			this.selectedList=[];
			this.ischeckAll=false;
		}
	}
</script>
<style lang='stylus' scoped>
#cart
	height:100%;
	header
		width:92%;
		padding:0 4%;
		height:1rem;
		font-size:.4rem;
		display:flex;
		align-items:center;
		background: linear-gradient(-90deg, #FF5000 0%, #FF8400 100%);
		z-index:2;
		transition:.2s;
		color:#fff;
		&.bgWhite
			background:#fff;
			color:#000;
		.iconfont
			font-size:.48rem;
		span
			position:absolute;
			left:50vw;
			transform:translateX(-50%);
			font-weight:700;
	.place
		height:1rem;
	.count
		background-image: linear-gradient(-90deg, #FF5000 0%, #FF8400 100%);
		height:16vh;
		p
			margin:0;
			padding:.2rem .3rem;
			color:#fff;
			.van-divider
				font-size:.34rem;
			.van-divider--hairline::after,.van-divider--hairline::before
				transform: scaleY(1);
	.empty
		font-size:.36rem;
		color:#a7a7a7;
		text-align:center;
		
	>>>.van-swipe-cell__right
		display:flex;
	>>>.van-swipe-cell 
		height:inherit;
	.van-checkbox-group
		margin-bottom:2.6rem;
		margin-top:-1rem;
	.row
		width: calc(92%);
		height: calc(22vw + 20px);
		margin: .36rem auto;
		border-radius: 7px;
		box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.2);
		background:#fff;
		overflow:hidden;
		.left
			font-size:.32rem;
			margin:0 .14rem ;
		.right
			display:flex;
			align-items:center;
			margin-right:.13rem;
			flex:2;
			img
				width: 22vw;
				height: 22vw;
				border-radius: 5px;
				margin-right:.13rem;
			.info
				font-size:.24rem;
				padding: 0 .14rem;
				position:relative;
				flex:2;
				p
					margin:.12rem 0;
				.title
					font-size:.28rem;
					display: -webkit-box;
					-webkit-box-orient: vertical;
					-webkit-line-clamp: 2;
					overflow: hidden;
				.price
					font-size:.28rem;
					color:red;
				.spec
					color:#a7a7a7;
					background:#f3f3f3;
					padding:.04rem .1rem;
					border-radius:.14rem;
			.van-stepper
				position: absolute;
				right: .2rem;
				bottom: .2rem;
		>>> .van-swipe-cell__right
			right:-1px;
	>>> .van-swipe-cell__wrapper
		height:inherit;
		display:flex;
		align-items:center;
	.van-button--square
		height: inherit;
		width:1.7rem;
		font-weight:700;
		font-weight:bold;
	.van-submit-bar
		bottom:1.09rem;
		.van-checkbox
			margin-left:.2rem;
	.van-submit-bar__bar
		position:relative;
		height:1rem;
		padding:0 .12rem;
		>>>.van-submit-bar__button
			width:1.8rem;
		>>>.van-checkbox__icon
			height:.4rem;
			.van-icon
				width:.4rem;
				height:.4rem;
				display:flex;
				align-items:center;
				justify-content:center;
		>>>.van-icon::before 
			width:96%;
		>>>.van-checkbox__label 
			margin-left:.12rem;
			font-size:.28rem;
		.settlement
			position:absolute;
			top:50%;
			transform:translateY(-50%);
			right:0;
			text-align:center;
			width:1.7rem;
			z-index:999;
			color:#fff;
			font-size:.3rem;
			i
				font-style:normal;
		.van-submit-bar__text 
			span 
				
		.van-button--hairline
			font-size:.26rem;
			padding:0;
			height:.6rem;
			width:.9rem;
			margin:0 .1rem;
			.van-button__text
				display:flex;
				align-items:center;
				justify-content:center;
				height:inherit;
				width:100%;
		.van-submit-bar__price
			font-size:.32rem;
			font-weight:700;
			word-break:break-all;
			text-align:left;
		.van-submit-bar__text 
			display:flex;
			align-items:center;
			justify-content:flex-end;
		.btn
			width: 34%;
			display:flex;
			align-items:center;

</style>


